import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';

import { FuseUtils } from '@fuse/utils';

import { Opportunity } from './opportunity.model';

@Injectable()
export class OpportunitiesService implements Resolve<any>
{
    onOpportunitiesChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSearchTextChanged: Subject<any> = new Subject();
    onSelectedOpportunitiesChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onUserDataChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onFilterChanged: Subject<any> = new Subject();

    opportunities: Opportunity[];
    selectedOpportunities: string[] = [];

    searchText: string;
    filterBy: string;

    constructor(private http: HttpClient)
    {
    }

    /**
     * The Opportunities App Main Resolver
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getOpportunities()
            ]).then(
                ([files]) => {

                    this.onSearchTextChanged.subscribe(searchText => {
                        this.searchText = searchText;
                        this.getOpportunities();
                    });

                    this.onFilterChanged.subscribe(filter => {
                        this.filterBy = filter;
                        this.getOpportunities();
                    });

                    resolve();

                },
                reject
            );
        });
    }

    getOpportunities(): Promise<any>
    {
        return new Promise((resolve, reject) => {
                this.http.get('api/opportunities')
                    .subscribe((response: any) => {

                        this.opportunities = response;

                        if ( this.searchText && this.searchText !== '' )
                        {
                            this.opportunities = FuseUtils.filterArrayByString(this.opportunities, this.searchText);
                        }

                        this.opportunities = this.opportunities.map(opportunity => {
                            return new Opportunity(opportunity);
                        });

                        this.onOpportunitiesChanged.next(this.opportunities);
                        resolve(this.opportunities);
                    }, reject);
            }
        );
    }


    updateOpportunity(opportunity)
    {
        return new Promise((resolve, reject) => {

            this.http.post('api/opportunities/' + opportunity.id, {...opportunity})
                .subscribe(response => {
                    this.getOpportunities();
                    resolve(response);
                });
        });
    }


    deleteOpportunity(opportunity)
    {
        const opportunityIndex = this.opportunities.indexOf(opportunity);
        this.opportunities.splice(opportunityIndex, 1);
        this.onOpportunitiesChanged.next(this.opportunities);
    }

    /**
     * Toggle selected contact by id
     * @param id
     */
    toggleSelectedOpportunity(id)
    {
        // First, check if we already have that todo as selected...
        if ( this.selectedOpportunities.length > 0 )
        {
            const index = this.selectedOpportunities.indexOf(id);

            if ( index !== -1 )
            {
                this.selectedOpportunities.splice(index, 1);

                // Trigger the next event
                this.onSelectedOpportunitiesChanged.next(this.selectedOpportunities);

                // Return
                return;
            }
        }

        // If we don't have it, push as selected
        this.selectedOpportunities.push(id);

        // Trigger the next event
        this.onSelectedOpportunitiesChanged.next(this.selectedOpportunities);
    }

    /**
     * Toggle select all
     */
    toggleSelectAll()
    {
        if ( this.selectedOpportunities.length === this.opportunities.length )
        {
            this.deselectOpportunities();
        }
        else
        {
            this.selectOpportunities();
        }
    }

    selectOpportunities(filterParameter?, filterValue?)
    {
        this.selectedOpportunities = [];

        // If there is no filter, select all todos
        if ( filterParameter === undefined || filterValue === undefined )
        {
            this.selectedOpportunities = [];
            this.opportunities.map(opportunity => {
                this.selectedOpportunities.push(opportunity.id);
            });
        }
        else
        {
            /* this.selectedContacts.push(...
                 this.contacts.filter(todo => {
                     return todo[filterParameter] === filterValue;
                 })
             );*/
        }

        // Trigger the next event
        this.onSelectedOpportunitiesChanged.next(this.selectedOpportunities);
    }

    deselectOpportunities()
    {
        this.selectedOpportunities = [];

        // Trigger the next event
        this.onSelectedOpportunitiesChanged.next(this.selectedOpportunities);
    }

    deleteSelectedOpportunities()
    {
        for ( const opportunityId of this.selectedOpportunities )
        {
            const opportunity = this.opportunities.find(_opportunity => {
                return _opportunity.id === opportunityId;
            });
            const opportunityIndex = this.opportunities.indexOf(opportunity);
            this.opportunities.splice(opportunityIndex, 1);
        }
        this.onOpportunitiesChanged.next(this.opportunities);
        this.deselectOpportunities();
    }

}



