import { Component } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';

import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

import { OpportunitiesService } from '../opportunities.service';

@Component({
    selector   : 'fuse-selected-bar',
    templateUrl: './selected-bar.component.html',
    styleUrls  : ['./selected-bar.component.scss']
})
export class OpportunitiesSelectedBarComponent
{
    selectedOpportunities: string[];
    hasSelectedOpportunities: boolean;
    isIndeterminate: boolean;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

    constructor(
        private opportunitiesService: OpportunitiesService,
        public dialog: MatDialog
    )
    {
        this.opportunitiesService.onSelectedOpportunitiesChanged
            .subscribe(selectedOpportunities => {
                this.selectedOpportunities = selectedOpportunities;
                setTimeout(() => {
                    this.hasSelectedOpportunities = selectedOpportunities.length > 0;
                    this.isIndeterminate = (selectedOpportunities.length !== this.opportunitiesService.opportunities.length && this.selectedOpportunities.length > 0);
                }, 0);
            });

    }

    selectAll()
    {
        this.opportunitiesService.selectOpportunities();
    }

    deselectAll()
    {
        this.opportunitiesService.deselectOpportunities();
    }

    deleteSelectedOpportunities()
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete all selected contacts?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.opportunitiesService.deleteSelectedOpportunities();
            }
            this.confirmDialogRef = null;
        });
    }

    emailSelected()
    {

    }

    massUpdateSelected()
    {

    }

    mergeSelected()
    {

    }

    addToTargetListSelected()
    {

    }

    generateLetterSelected()
    {
        
    }
}



