import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { Subscription } from 'rxjs/Subscription';
import { MatDialog } from '@angular/material';

import { fuseAnimations } from '@fuse/animations';

import { OpportunityFormDialogComponent } from './opportunity-form/opportunity-form.component';
import { OpportunitiesService } from './opportunities.service';

@Component({
    selector     : 'opportunities-opportunities',
    templateUrl  : './opportunities.component.html',
    styleUrls    : ['./opportunities.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class OpportunitiesComponent implements OnInit, OnDestroy
{
    selectedCount = 0;
    searchInput: FormControl;
    dialogRef: any;
    onSelectedOpportunitiesChangedSubscription: Subscription;

    constructor(
        private opportunitiesService: OpportunitiesService,
        public dialog: MatDialog
    )
    {
        this.searchInput = new FormControl('');
    }

    ngOnInit()
    {
        this.onSelectedOpportunitiesChangedSubscription =
            this.opportunitiesService.onSelectedOpportunitiesChanged
                .subscribe(selectedContacts => {
                    this.selectedCount = selectedContacts.length;
                });

        this.searchInput.valueChanges
            .debounceTime(300)
            .distinctUntilChanged()
            .subscribe(searchText => {
                this.opportunitiesService.onSearchTextChanged.next(searchText);
            });
    }

    ngOnDestroy()
    {
    }

    newOpportunity()
    {
        this.dialogRef = this.dialog.open(OpportunityFormDialogComponent, {
            panelClass: 'opportunity-form-dialog',
            data      : {
                action: 'new'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }

                this.opportunitiesService.updateOpportunity(response.getRawValue());

            });

    }

    deleteSelectedOpportunities() {
        this.opportunitiesService.deleteSelectedOpportunities();
    }
}



