import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';

import {
    MatButtonModule,
    MatCheckboxModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatRippleModule,
    MatSidenavModule,
    MatSortModule,
    MatTableModule,
    MatToolbarModule,
    MatTabsModule,
    MatOptionModule,
    MatSelectModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';

import { OpportunitiesComponent } from './opportunities.component';
import { OpportunitiesService } from './opportunities.service';
import { OpportunityListComponent } from './opportunity-list/opportunity-list.component';
import { OpportunityFormDialogComponent } from './opportunity-form/opportunity-form.component';
import { OpportunitiesSelectedBarComponent } from './selected-bar/selected-bar.component';
import { OpportunitiesMainSidenavComponent } from './opportunity-sidebar/opportunity-sidebar.component';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { ChartsModule } from 'ng2-charts';

const routes: Routes = [
    {
        path     : '**',
        component: OpportunitiesComponent,
        resolve  : {
            contacts: OpportunitiesService
        }
    }
];

@NgModule({
    declarations   : [
        OpportunitiesComponent,
        OpportunityListComponent,
        OpportunityFormDialogComponent,
        OpportunitiesSelectedBarComponent,
        OpportunitiesMainSidenavComponent
    ],
    imports        : [
        RouterModule.forChild(routes),
        CdkTableModule,

        MatButtonModule,
        MatCheckboxModule,
        MatDatepickerModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSidenavModule,
        MatSortModule,
        MatTableModule,
        MatToolbarModule,
        MatTabsModule,
        MatOptionModule,
        MatSelectModule,
        ChartsModule,
        NgxChartsModule,

        FuseSharedModule,
        FuseConfirmDialogModule
    ],
    providers      : [
        OpportunitiesService
    ],
    entryComponents: [OpportunityFormDialogComponent]
})
export class OpportunitiesModule
{
}



