import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation, ElementRef } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef, MatPaginator, MatSnackBar, MatSort } from '@angular/material';
import { DataSource, SelectionModel } from '@angular/cdk/collections';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

import { OpportunityFormDialogComponent } from '../opportunity-form/opportunity-form.component';
import { OpportunitiesService } from '../opportunities.service';
import { FuseUtils } from '@fuse/utils';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Opportunity } from '../opportunity.model';

@Component({
    selector     : 'opportunities-opportunity-list',
    templateUrl  : './opportunity-list.component.html',
    styleUrls    : ['./opportunity-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class OpportunityListComponent implements OnInit, OnDestroy
{
    @ViewChild('dialogContent') dialogContent: TemplateRef<any>;
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
    
    opportunities: any;
    dataSource: OpportunitiesDataSource | null;
    displayedColumns = ['checkbox', 'name', 'accountName', 'contactName', 'leadSource', 'type', 'salesStage', 'amount', 'closeDate', 'user', 'dateCreated', 'buttons'];
    selectedOpportunities: any[];
    checkboxes: {};

    onOpportunitiesChangedSubscription: Subscription;
    onSelectedOpportunitiesChangedSubscription: Subscription;

    dialogRef: any;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

    constructor(
        private opportunitiesService: OpportunitiesService,
        public dialog: MatDialog,
        private snackBar: MatSnackBar,
    )
    {
        this.onOpportunitiesChangedSubscription =
            this.opportunitiesService.onOpportunitiesChanged.subscribe(opportunities => {

                this.opportunities = opportunities;

                this.checkboxes = {};
                opportunities.map(contact => {
                    this.checkboxes[contact.id] = false;
                });
            });

        this.onSelectedOpportunitiesChangedSubscription =
            this.opportunitiesService.onSelectedOpportunitiesChanged.subscribe(selectedOpportunities => {
                for ( const id in this.checkboxes )
                {
                    if ( !this.checkboxes.hasOwnProperty(id) )
                    {
                        continue;
                    }

                    this.checkboxes[id] = selectedOpportunities.includes(id);
                }
                this.selectedOpportunities = selectedOpportunities;
            });


    }

    ngOnInit()
    {
        this.dataSource = new OpportunitiesDataSource(
            this.opportunitiesService,
            this.paginator,
            this.sort
        );

    }

    ngOnDestroy()
    {
        this.onOpportunitiesChangedSubscription.unsubscribe();
        this.onSelectedOpportunitiesChangedSubscription.unsubscribe();

    }

    editOpportunity(opportunity)
    {
        this.dialogRef = this.dialog.open(OpportunityFormDialogComponent, {
            panelClass: 'opportunity-form-dialog',
            data      : {
                opportunity: opportunity,
                action : 'edit'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.opportunitiesService.updateOpportunity(formData.getRawValue());

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteOpportunity(opportunity);

                        break;
                }
            });
    }

    /**
     * Delete Opportunity
     */
    deleteOpportunity(opportunity)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.opportunitiesService.deleteOpportunity(opportunity);
            }
            this.confirmDialogRef = null;
        });

    }

    onSelectedChange(opportunityId)
    {
        this.opportunitiesService.toggleSelectedOpportunity(opportunityId);
    }

    toggleAll(ev) {
        if (ev) {
            this.opportunitiesService.toggleSelectAll();
        }
    }
}


export class OpportunitiesDataSource extends DataSource<any>
{
    total = 0;

    private sub1: Subscription;

    constructor(
        private opportunitiesService: OpportunitiesService,
        private _paginator: MatPaginator,
        private _sort: MatSort
    ) {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<Opportunity[]>
    {
        const displayDataChanges = [
            this.opportunitiesService.onOpportunitiesChanged,
            this._paginator.page,
            this._sort.sortChange
        ];


        this.sub1 = this.opportunitiesService.onOpportunitiesChanged
            .subscribe(opportunities => {
                setTimeout(() => {
                    this.total = opportunities.length;
                });
            });

        return Observable.merge(...displayDataChanges)
            .map(() => {
                let opportunities = this.opportunitiesService.opportunities;

                opportunities = this.sortData(opportunities);

                const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
                opportunities = opportunities.slice(startIndex, startIndex + this._paginator.pageSize);

                return opportunities;
            });
    }

    sortData(data): Opportunity[] {
        if ( !this._sort.active || this._sort.direction === '' )
        {
            return data;
        }

        return data.sort((a, b) => {
            let valueA, valueB;

            switch(this._sort.active) {
                case 'dateCreated':
                    valueA = new Date(a[this._sort.active]);
                    valueB = new Date(b[this._sort.active]);
                    break;
                default:
                    valueA = a[this._sort.active];
                    valueB = b[this._sort.active];
                    break;
            }

            return (valueA < valueB ? -1 : 1) * (this._sort.direction === 'asc' ? 1 : -1);
        });
    }

    disconnect()
    {
        this.sub1.unsubscribe();
    }
}



